* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.signInWrapper {
  width: 1%;
  height: 100vh;
  background: #ededed;
  display: table-cell;
  vertical-align: middle;
  font-family: "Dosis", sans-serif;
}

.sign-panels {
  width: 650px;
  background: rgb(216, 216, 216);
  padding: 40px 80px;
  margin: 40px auto;
  border-radius: 20px;
  text-align: center;
}
.login,
.signup {
  position: relative;
  background-color: rgb(216, 216, 216);
}

.title {
  color: #9f9f9f;
}

.title span {
  display: block;
  font-size: 46px;
  font-weight: bold;
  color: #343a40;
}

.title p {
  font-size: 20px;
  font-weight: 500;
}

.sign-panels input,
select {
  width: 100%;
  display: block;
  margin-bottom: 15px;
  height: 50px;
  border-radius: 20px;
  border: none;
  background: #f1f0f0;
  text-align: center;
  padding: 10px;
  font-size: 15px;
  color: #7c7c7c;
  font-weight: 500;
  text-align-last: center;
}

.sign-panels input:focus {
  outline: none;
}

.sign-panels input[type="checkbox"] {
  display: none;
}

.sign-panels input[type="checkbox"] + label {
  display: block;
  width: 50%;
  text-align: left;
  padding-left: 60px;
  cursor: pointer;
  color: #828282;
  font-weight: 500;
  margin-top: 10px;
  float: left;
  height: 50px;
  padding-top: 15px;
}

.sign-panels input[type="checkbox"] + label:before {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  background: #dbdbdb;
  position: absolute;
  left: 30px;
  border-radius: 50%;
  border: 2px solid white;
  box-shadow: 0 0 0 5px #ededed;
}

.sign-panels input[type="checkbox"]:checked + label:before {
  background: #ff5722;
  box-shadow: 0 0 0 5px #ff5722;
}

.btn-signin {
  display: inline-block;
  width: 50%;
  margin-top: 10px;
  height: 50px;
  background: #ec581e;
  border-radius: 50px;
  padding: 11px;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.btn-reset,
.btn-member,
.btn-fade {
  font-size: 19px;
  font-weight: 500;
  color: #9f9f9f;
  display: block;
  /*width: 210px;*/
  margin: 30px auto 0;
  text-decoration: none;
}
.btn-member {
  margin-top: 15px;
}

.btn-reset .fa,
.btn-member .fa {
  margin-left: 6px;
}

.notification p {
  font-size: 20px;
  font-weight: 600;
  color: #9f9f9f;
}

.notification span {
  color: #ec581e;
}

.error {
  display: block;
  color: #ec581e;
  font-size: 20px;
  font-weight: 600;
  margin: 15px 0;
}

@media screen and (max-width: 768px) {
  .sign-panels {
    width: 90%;
    padding: 40px;
  }
}

@media screen and (max-width: 570px) {
  .sign-panels {
    padding: 40px 20px;
  }

  .btn-face,
  .btn-twitter {
    width: 100%;
  }
  .btn-face {
    margin-right: 0;
    margin-bottom: 25px;
  }
}

@media screen and (max-width: 480px) {
  .sign-panels input[type="checkbox"] + label {
    width: 100%;
  }

  .btn-signin {
    width: 80%;
  }

  .title span {
    font-size: 36px;
  }
}

.validation {
  color: red;
  font-size: medium;
}
form {
